html {
    box-sizing: border-box;
    overflow-x: hidden;
    scroll-behavior: smooth;
  }
  
  *,
  *::before,
  *::after {
    box-sizing: inherit;
  }
  
  body {
    font-family: $font-roboto;
    font-size: 16px;
    line-height: 18px;
    color: grey;
    font-weight: normal;
  }
  
  a {
    text-decoration: none;
  }
  
  img {
    max-width: 100%;
    height: auto;
    vertical-align: middle;
  }